/* Universal */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-decoration: none;
  font-family: 'Inter', 'sans-serif';
}

#root {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: flex-start;
}

.container {
  margin: auto;
}

/* Animation */
@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Typography */

.logo {
  font-size: .6rem;
}

@media (min-width: 750px) {
  .logo {
    font-size: 1rem;
  }
}

nav a {
  color: #FFFFFF;
  font-size: 4vw;
  margin-left: 1em;
  border-bottom: 2px #000000 solid;
}

nav a:hover, nav a:focus {
  border-bottom: 2px #FFFFFF solid;
}


.link {
  font-weight: 500;
  font-size: 1rem;
}

.notice a {
  color: #000000;
  text-decoration: underline;
}

.products-item *, .cart-item-text, .product-info * {
  font-weight: 400;
}

.cart-item-text {
  font-size: 1rem;
  margin: .5em 0;
}

.artist {
  color: #666666;
}

/* Images */

#cart-image {
  margin-right: .2em;
  width: 1em;
  transform: translateY(-10%);
}

.badge {
  padding: .2rem;
  border-radius: 50%;
  font-weight: 500;
  font-size: .75rem;
  background: #FF3300;
  color: #fefefe;
}

.products-item-image, .carousel-item img, .cart-item-image {
  max-width: 100%;
  object-fit: cover;
}

.cart-item-image {
  width: 30%;
}

.products-item-image {
  order: -1;
}

/* Buttons */

/* TODO: Add show-tracklist-button styling */

.add-cart-button {
  background-color: #000000;
  color: #FFFFFF;
  border-radius: 10px;
  border: none;
  font-weight: 600;
  padding: .25em 1em;
  cursor: pointer;
}

.cart-button {
  background-color: #000000;
  color: #FFFFFF;
  border: none;
  font-weight: 600;
  padding: .5em;
  cursor: pointer;
}

.add-cart-button:hover, .add-cart-button:focus,
.cart-button:hover, .cart-button:focus,
.cart-button:hover, .cart-button:focus {
  background-color: #ffd561;
  color: #000000;
}

.cart-item-remove {
  cursor: pointer;
}

.cart-item-remove:hover {
  text-decoration: underline;
}

.cart-button.cart-button-left {
  margin-left: 0;
}

.cart-button.cart-button-right {
  margin-right: 0;
}

/* Forms */



/* nav */

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1.3em;
  width: 100%;
  background-color: #000000;
}

.right-nav {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.logo {
  margin-left: 0;
}

.link-cart {
  display: flex;
}

/* Notice */

.notice {
  width: 100%;
  background-color: #ffd561;
  text-align: center;
  padding: .2em;
}

/* Footer */

footer {
  margin-top: auto;
  width: 100%;
  background-color: #000000;
  color: #FFFFFF;
  text-align: center;
  padding: .5em;
}

/* Home Page */
.carousel-item {
  margin: auto auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  animation: fadein 1s;
}

.hidden {
  display: none;
}

@media (min-width: 1000px) {
  .carousel-item {
    flex-direction: row;
    gap: 1em;
  }
}


/* Products Page */

.records {
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadein 1s;
}

@media (min-width: 750px) {
  .records {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
}

@media (min-width: 1500px) {
  .records {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.product-link {
  width: 90%;
  text-decoration: none;
  color: #000000;
}

.products-item {
  margin: 1em 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: solid 2px #000000;
  padding: .5em;
  cursor: pointer;
}

.products-item:hover {
  background-color: #f2f2f2;
}

.pricing {
  margin-top: .75em;
  display: flex;
  justify-content: space-between;
}

.products-add {
  display: flex;
  justify-content: space;
}

.products-quantity-buttons {
  display: flex;
  align-items: center;
}


/* TODO: Rename some buttons (add-cart) and divs (quantity) to fix confusion */
/* TODO: Re-organise the CSS */

.quantity {
  text-align: center;
  border: 2px solid #000000;
  width: 4em;
  font-weight: 600;
  padding: .35em;

}

/* Product Info Page */

.product-info-container {
  display: flex;
  justify-content: center;
}

.product-info {
  width: 90%;
  margin: 1em 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.5em;
  gap: 1em;
}

.products-item-description {
  max-width: 600px;
}

@media (min-width: 1500px) {
  .product-info-container {
    display: flex;
    justify-content: center;
  }


  .product-info {
    width: 100%;
    flex-direction: row;
  }

  /* TODO: Rename product-info and product-info-text */

  .product-info-text {
    display: flex;
    flex-direction: column;
    gap: 1em;
    align-items: flex-start;
  }
}

/* Contact Page */

.contact {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  animation: fadein 1s;
}

.contact h1 {
  font-size: 3rem;
  text-align: center;
}

.contact-bar {
  display: flex;
  gap: 1rem;
  align-items: center;
  align-self: center;
  color: black;
}

.contact-bar-item {
  color: black;
}

.contact-bar-item:hover, .contact-bar-item:focus {
  color: #ffd561;
}

/* Cart Page */

.cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadein 1s;
}

.cart-total {
  text-align: right;
  margin: auto 1em;
  align-self: flex-end;
}

.cart-buttons {
  margin: 1em;
  align-self: flex-end;
}

.cart-button {
  margin: 0 .5em;
}



/* Cart Item */

.cart-item {
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1em;
  padding: 1em 0;
  border-bottom: 1px #000000 solid;
}

.cart-item-info {
  margin-left: .5em;
}

.cart-item-quantity {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart-item-pricing {
  display: flex;
  flex-direction: column;
  gap: .75em;
  text-align: right;
}

/* Cart Empty */
.cart-empty-message {
  margin-bottom: 1em;
  text-align: center;
}